/* The switch - the box around the slider */
.unicorn-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-bottom: 0;
  /* Hide default HTML checkbox */
  /* The slider */
  /* Rounded sliders */
}
.unicorn-switch input {
  width: 0;
  height: 0;
  position: relative;
  left: 30px;
}
.unicorn-switch input:focus {
  box-shadow: none !important;
}
.unicorn-switch .switch-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  background-color: var(--bs-btn-bg);
}
.unicorn-switch .switch-slider:hover {
  background-color: var(--bs-btn-hover-bg);
}
.unicorn-switch .switch-slider.btn-primary {
  background-color: var(--bs-btn-bg, var(--bs-primary, var(--primary)));
}
.unicorn-switch .switch-slider.btn-primary:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-primary, var(--primary)));
}
.unicorn-switch .switch-slider.btn-secondary {
  background-color: var(--bs-btn-bg, var(--bs-secondary, var(--secondary)));
}
.unicorn-switch .switch-slider.btn-secondary:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-secondary, var(--secondary)));
}
.unicorn-switch .switch-slider.btn-success {
  background-color: var(--bs-btn-bg, var(--bs-success, var(--success)));
}
.unicorn-switch .switch-slider.btn-success:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-success, var(--success)));
}
.unicorn-switch .switch-slider.btn-info {
  background-color: var(--bs-btn-bg, var(--bs-info, var(--info)));
}
.unicorn-switch .switch-slider.btn-info:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-info, var(--info)));
}
.unicorn-switch .switch-slider.btn-danger {
  background-color: var(--bs-btn-bg, var(--bs-danger, var(--danger)));
}
.unicorn-switch .switch-slider.btn-danger:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-danger, var(--danger)));
}
.unicorn-switch .switch-slider.btn-warning {
  background-color: var(--bs-btn-bg, var(--bs-warning, var(--warning)));
}
.unicorn-switch .switch-slider.btn-warning:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-warning, var(--warning)));
}
.unicorn-switch .switch-slider.btn-dark {
  background-color: var(--bs-btn-bg, var(--bs-dark, var(--dark)));
}
.unicorn-switch .switch-slider.btn-dark:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-dark, var(--dark)));
}
.unicorn-switch .switch-slider.btn-light {
  background-color: var(--bs-btn-bg, var(--bs-light, var(--light)));
}
.unicorn-switch .switch-slider.btn-light:hover {
  background-color: var(--bs-btn-hover-bg, var(--bs-light, var(--light)));
}
.unicorn-switch input:focus + .switch-slider {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.unicorn-switch .switch-slider.bg-default {
  background-color: #ccc;
}
.unicorn-switch .switch-slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.25s;
}
.unicorn-switch input:checked + .switch-slider:before {
  transform: translateX(26px);
}
.unicorn-switch input:not(:checked) + .switch-slider {
  background-color: #ccc !important;
}
.unicorn-switch input:not(:checked) + .switch-slider:hover {
  background-color: #aaa !important;
}
.unicorn-switch input[disabled] + .switch-slider,
.unicorn-switch input[readonly] + .switch-slider {
  opacity: 0.65;
  cursor: not-allowed;
  pointer-events: none;
}
.unicorn-switch .switch-slider.slider-round {
  border-radius: 0.25rem;
}
.unicorn-switch .switch-slider.slider-round:before {
  border-radius: 0.18rem;
}
.unicorn-switch .switch-slider.slider-circle {
  border-radius: 50px;
}
.unicorn-switch .switch-slider.slider-circle:before {
  border-radius: 100px;
}
.unicorn-switch.switch-sm {
  width: 46px;
  height: 24px;
}
.unicorn-switch.switch-sm .switch-slider:before {
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
}
.unicorn-switch.switch-sm input:checked + .switch-slider:before {
  transform: translateX(22px);
}
.unicorn-switch.switch-lg {
  width: 100px;
  height: 34px;
}
.unicorn-switch.switch-lg .switch-slider:before {
  height: 26px;
  width: 45px;
  left: 4px;
  bottom: 4px;
}
.unicorn-switch.switch-lg input:checked + .switch-slider:before {
  transform: translateX(47px);
}

.has-invalid .unicorn-switch .switch-slider {
  border: 1px solid #dc3545;
}